import { emergencyT } from "@/Util";
import { Button, Checkbox, Col, Form, Input, Modal, Row, Select } from "antd";
import { CheckboxChangeEvent } from "antd/lib/checkbox";
import TextArea from "antd/lib/input/TextArea";
import { DefaultOptionType } from "antd/lib/select";
import { debug } from "console";
import dayjs from "dayjs";
import React, { useState } from "react";

const { Option } = Select;

export default function EditModel(props: { data: emergencyT; onOk: (data: emergencyT) => void }) {
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [data, setData] = useState(props.data);

    const showModal = () => {
        setIsModalVisible(true);
    };

    const handleOk = () => {
        props.onOk(data);
        setIsModalVisible(false);
    };

    const handleCancel = () => {
        setIsModalVisible(false);
    };

    function sloganChange(value: string, option: DefaultOptionType | DefaultOptionType[]) {}

    function isReportChange(e: CheckboxChangeEvent) {
        let a = Object.assign({}, data, { isReported: e.target.checked });
        console.log(a);
        setData(a);
    }

    function reportContentChange(e: React.ChangeEvent<HTMLTextAreaElement>) {
        let a = Object.assign({}, data, { reportContent: e.target.value + "" });
        console.log(a);
        setData(a);
    }

    function OnSpeechFinish(text: string) {
        let a = Object.assign({}, data, { reportContent: data.reportContent + text });
        console.log(a);
        setData(a);
    }

    return (
        <>
            <Button type="primary" onClick={showModal}>
                填寫
            </Button>
            <Modal title="簡訊回覆" visible={isModalVisible} onOk={handleOk} onCancel={handleCancel}>
                <Form name="basic" labelCol={{ span: 8 }} wrapperCol={{ span: 16 }} autoComplete="off">
                    <Form.Item label="">
                        編號：{data.medicalRecordNumber}
                        <br />
                        院區：{data.hospital}
                        <br />
                        醫檢師：{data.medicalExaminer}
                        <br />
                        病歷號：{data.medicalRecordNumber}
                        <br />
                        病患名稱：{data.patientName}
                        <br />
                    </Form.Item>
                    <Form.Item label="">
                        簡訊時間：{dayjs(data.notificationDateTime).format("YYYY/MM/DD HH:mm")}
                        <br />
                        簡訊內容：{data.notificationContent}
                    </Form.Item>
                    <Form.Item label="">
                        片語：
                        <br />
                        <Select defaultValue="==請選擇==" style={{ width: 120 }} onChange={sloganChange}>
                            <Option value="片語1">片語1</Option>
                            <Option value="片語2">片語2</Option>
                            <Option value="片語3">片語3</Option>
                        </Select>
                    </Form.Item>
                    <Form.Item label="">
                        通知病人回診：<Checkbox onChange={isReportChange} checked={data.isReported}></Checkbox>
                    </Form.Item>
                    <Form.Item label="">
                        回覆：
                        <br />
                        <TextArea onChange={reportContentChange} value={data.reportContent} defaultValue={data.reportContent} rows={4} />
                    </Form.Item>
                </Form>
            </Modal>
        </>
    );
}
