import { Col, Divider, Input, Radio, RadioChangeEvent, Row, Select, Space, Table } from "antd";
import { useEffect, useState } from "react";
import { ColumnsType } from "antd/lib/table";
import dayjs from "dayjs";
import DatePicker from "./DatePicker";
import { DefaultOptionType } from "antd/lib/select";
import EditModel from "./EditModel";
import { emergencyT } from "@/Util";

const { Option } = Select;

export default function MainTable() {
    const [dataSource, setDataSource] = useState<emergencyT[]>([]);

    const [report, setReport] = useState<string>('A');

    const [loading, setLoading] = useState(false);

    const columns: ColumnsType<emergencyT> = [
        {
            title: "功能",
            dataIndex: "medicalRecordNumber",
            render(value, record, index) { return <EditModel data={record} onOk={onOk} /> }
        },
        {
            title: "病歷號",
            dataIndex: "medicalRecordNumber",
        },
        {
            title: "病患名稱",
            dataIndex: "patientName",
        },
        {
            title: "醫檢師",
            dataIndex: "medicalExaminer",
            responsive: ["md"]
        },
        {
            title: "通知內容",
            dataIndex: "notificationContent",
        },
        {
            title: "通知時間",
            dataIndex: "notificationDateTime",
            render(value: Date, record, index) {
                return dayjs(value).format("MM/DD HH:mm");
            },
            responsive: ["md"]
        },
        {
            title: "回報內容",
            dataIndex: "reportContent",
            ellipsis: true,
        },
    ];


    const reportChange = (e: RadioChangeEvent) => {
        console.log("radio checked", e.target.value);
        setReport(e.target.value);
    };

    const sourceChange = (value: string, option: DefaultOptionType | DefaultOptionType[]) => {
    }
    function onOk(e: emergencyT){
        setDataSource(dataSource.map(value => e.medicalRecordNumber === value.medicalRecordNumber ? e : value))
    }

    useEffect(() => {

    }, []);

    return (
        <>
            <Divider />
            <Row justify="start" align="middle">
                <Col >
                    <Radio.Group onChange={reportChange} value={report} size="large" >
                        <Space direction="vertical">
                            <Radio value={'A'}>全部　</Radio>
                            <Radio value={'N'}>未回覆</Radio>
                            <Radio value={'Y'}>已回復</Radio>
                        </Space>
                    </Radio.Group>
                </Col>

                <Col>
                    <Row justify="start" align="middle">
                        <Col>
                            報告日期：<DatePicker /><DatePicker />
                        </Col>
                    </Row>
                    <Row justify="start" align="middle">
                        <Col>
                            被通報人：<Input placeholder="input search text" style={{ width: 200 }} />
                        </Col>
                    </Row>
                    <Row justify="start" align="middle">
                        檢查科室：<Select defaultValue="==科室==" style={{ width: 120 }} onChange={sourceChange}>
                            <Option value="科室1">科室1</Option>
                            <Option value="科室2">科室2</Option>
                            <Option value="科室3">科室3</Option>
                        </Select>
                    </Row>
                </Col>
            </Row>
            <Divider />
            <Table
                size="small"
                rowKey={"medicalRecordNumber"}
                loading={loading}
                dataSource={dataSource}
                columns={columns}
            />
        </>
    );
}
